import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { Dimmer, Loader, Container, Sidebar, Segment, Button, Menu, Image, Icon, Header, Accordion } from 'semantic-ui-react'
import CtrlPanel from './CtrlPanel'
import DataPanel from './DataPanel'
import ComplexMap from './ComplexMap'

class App extends Component {
  state = {
    ctrlPanelVisible: false,
    dataPanelvisible: false,
    ctrlAccordionIndex: 0,
    loaderOn: false,
    detectionType: 'detections',
    wells: 'all'
  }

  handleAccordionClick = (e, titleProps) => {
    const { index } = titleProps
    const { ctrlAccordionIndex } = this.state
    const newIndex = ctrlAccordionIndex === index ? -1 : index

    this.setState({ ctrlAcoordionIndex: newIndex })
  }

  toggleVisibility = () => this.setState({ ctrlPanelVisible: !this.state.ctrlPanelVisible })
  handleItemClick = (e, { name }) => {
    this.setState({ activeItem: name })
    if (name === 'Ctrl Panel') {
      this.setState({ ctrlPanelVisible: !this.state.ctrlPanelVisible })
    }

    if (name === 'Data Panel') {
      this.setState({ dataPanelvisible: !this.state.dataPanelvisible })
    }
  }

  onZoneQuerySubmit = (data) => {
    console.log("onZoneQuerySubmit:");
    console.log(data);
    // <args><ParamGroup>Metals</ParamGroup><Param>Barium</Param><Zone>A</Zone><Event>2015-08-01</Event></args>
    let body = '<args><ParamGroup>' + data.explorerParamGroupsZone
      + '</ParamGroup><Param>' + data.explorerParamZone
      + '</Param><Zone>' + data.zone
      + '</Zone><Event>' + data.eventDateZone
      + '</Event></args>'

    let postBody = {
      Event: '/Date('+ new Date(data.eventDateZone).getTime().toString() + ')/',      
      Param: data.explorerParamZone
    }

    console.log('body ' + JSON.stringify(postBody))
    fetch('http://54.213.115.18/reachwcfrest/EAGLE_MPSW.svc/GetZoneBasedMapQueryResult/', {
      method: 'POST',
      headers: { "Content-Type": "application/json", "Accept": "application/json" },
      body: JSON.stringify(postBody)
    })
      .then((response) => response.json())
      .then((responseJson) => {
        this.setState({ loaderOn: false })
        let res = JSON.parse(responseJson)

        var dataMap = new Map()
        for (var ele of res) {
          if (dataMap.has(ele['Location']) === false) {
            var paramMap = new Map()
            dataMap.set(ele['Location'], ele['Result'])
          }
        }
        this.simpleMap.setTextLabels(dataMap)
      })
      .catch((error) => {
        this.setState({ loaderOn: false })
        console.error(error);
      });
  }

  onTimeQuerySubmit = (data) => {
    console.log("onTimeQuerySubmit:");
    console.log(data);
    this.setState({ loaderOn: true })

    let bd = '<args><WellType>' + data.wells + '</WellType><DetectionType>' + data.detectionType +
      '</DetectionType><PeriodType>' + data.period.toLowerCase() + '</PeriodType><PeriodString>' + data.eventDate.toString().toLowerCase() +
      '</PeriodString><ParamGroup>' + data.explorerParamGroups.toLowerCase() +
      '</ParamGroup><Param>' + data.explorerParam.toLowerCase() + '</Param></args>'

    let postBody = {
      StationType: data.wells,
      DetectionType: data.detectionType,
      PeriodType: data.period,
      Events: [],
      ParamGroup: data.explorerParamGroups,
      Param: data.explorerParam
    }
    console.log(JSON.stringify(postBody))

    fetch('http://54.213.115.18/ReachWcfRest/EAGLE_MPSW.svc/GetPeriodBasedMapQueryResult/', {
      method: 'POST',
      headers: { "Content-Type": "application/json", "Accept": "application/json" },
      body: JSON.stringify(postBody)
    })
      .then((response) => response.json())
      .then((responseJson) => {
        this.setState({ loaderOn: false })
        this.setState({ wells: data.wells.toLowerCase() })
        this.setState({ detectionType: data.detectionType.toLowerCase() })
        let res = JSON.parse(responseJson)
        var dataMap = new Map()
        for (var ele of res) {
          if (dataMap.has(ele['Location']) === false) {
            var paramMap = new Map()
            paramMap.set(ele['Parameter'], ele);
            dataMap.set(ele['Location'], paramMap)
          }
          else {
            var m = dataMap.get(ele['Location']);
            m.set(ele['Parameter'], ele)
            dataMap.set(ele['Location'], m)
          }
        }
        console.log(dataMap)
        this.dataPanel.setData(dataMap);
        this.simpleMap.setHightlighters(dataMap, this.state.wells, this.state.detectionType)
      })
      .catch((error) => {
        this.setState({ loaderOn: false })
        console.error(error);
      });
  }

  onDataPanelSiteClicked = (site) => {
    this.simpleMap.setSite(site)
  }

  onShowStations = (show) => {
    this.simpleMap.displayStations(show)
  }

  onShowBaseMap = (show) => {
    this.simpleMap.displayBasemap(show)
  }

  render() {
    const { ctrlPanelVisible } = this.state
    const { dataPanelvisible } = this.state
    const { activeItem } = this.state
    const { ctrlAccordionIndex } = this.state
    const { loaderOn } = this.state

    return (
      <div style={{ height: '100%' }}>
        {/* <Button onClick={this.toggleVisibility}>Toggle Visibility</Button> */}
        <Menu>
          <Menu.Item header>Eagle Mine</Menu.Item>
          <Menu.Item
            name='Ctrl Panel'
            active={ctrlPanelVisible}
            onClick={this.handleItemClick}
          >
            Ctrl Panel
          </Menu.Item>
          <Menu.Item
            name='Data Panel'
            active={dataPanelvisible}
            onClick={this.handleItemClick}
          >
            Data Panel
          </Menu.Item>
        </Menu>
        <Sidebar.Pushable as={Segment}>
          <Dimmer active={loaderOn}>
            <Loader>Loading</Loader>
          </Dimmer>
          <Sidebar as={Menu} animation='push' width='wide' visible={ctrlPanelVisible} icon='labeled' vertical >
            <CtrlPanel onTimeQuerySubmit={this.onTimeQuerySubmit} onZoneQuerySubmit={this.onZoneQuerySubmit}
              onShowBaseMap={this.onShowBaseMap} onShowStations={this.onShowStations} />
          </Sidebar>
          <Sidebar as={Menu} animation='push' width='wide' direction='right' visible={dataPanelvisible} icon='labeled' vertical >
            <DataPanel ref={panel => { this.dataPanel = panel; }} siteClicked={this.onDataPanelSiteClicked} />
          </Sidebar>
          <Sidebar.Pusher>
            <ComplexMap ref={panel => { this.simpleMap = panel; }} google={window.google} />
          </Sidebar.Pusher>
        </Sidebar.Pushable>

      </div>
    )
  }
}

export default App;



