import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import { Map, InfoWindow, Marker, Polygon, GoogleApiWrapper } from 'google-maps-react';
import DownStations from './data/background_and_reference.json'
import ComplianceStations from './data/compliance.json'
import Fence from './data/facility_fence.json'

export class ComplexMap extends Component {
    state = {
        center: {
            lat: 46.7487177, lng: -87.884195
        },
        showStation: true,
        showBasemap: true,
        hightlighters: [],
        wells: '',
        detectionType: '',
        period: '',
        paramGroup: '',
        param: '',
        colors: {
            highlighter: {
                stroke: {
                    down: '#dbdb4e',
                    compl: '#e5e5ae'
                },
                fill: {
                    down: '#dbdb4e',
                    compl: '#e5e5ae'
                }
            }
        },
        target: {
            name: '',
            coord: { lat: 0, lng: 0 }
        },
        zoneLabels: null
    }    

    setTextLabels = (data) => {
        console.log('setTextLabels')
        this.setState({ zoneLabels: data })
        console.log(this.state.zoneLabels)

        DownStations['features'].reduce((prev, curr, index, arr) => {
            var corrd = { lat: curr['geometry']['coordinates'][1], lng: curr['geometry']['coordinates'][0] }
            var name = curr['properties']['STATION_ID']
            if (this.state.zoneLabels.has(name)) {
                console.log(this.state.zoneLabels.get(name).toString())
            }
        }, {})
    }

    setHightlighters = (data, wells, detectionType) => {
        console.log(wells + ' ' + detectionType)

        if (detectionType === 'detection') {
            this.setState(prevState => ({
                colors: {
                    ...prevState.colors,
                    highlighter: {
                        ...prevState.colors.highlighter,
                        stroke: {
                            ...prevState.colors.highlighter.stroke,
                            down: '#dbdb4e',
                            coml: '#dbdb4e'
                        },
                        fill: {
                            ...prevState.colors.highlighter.fill,
                            down: '#e5e5ae',
                            coml: '#e5e5ae'
                        }
                    }

                }
            }))
        }
        else if (detectionType === 'abovebm') {
            this.setState(prevState => ({
                colors: {
                    ...prevState.colors,
                    highlighter: {
                        ...prevState.colors.highlighter,
                        stroke: {
                            ...prevState.colors.highlighter.stroke,
                            down: '#3936ed',
                            coml: '#60f260'
                        },
                        fill: {
                            ...prevState.colors.highlighter.fill,
                            down: '#0b08ce',
                            coml: '#1add1a'
                        }
                    }

                }
            }))
        }
        else if(detectionType === 'exceedance'){
            this.setState(prevState => ({
                colors: {
                    ...prevState.colors,
                    highlighter: {
                        ...prevState.colors.highlighter,
                        stroke: {
                            ...prevState.colors.highlighter.stroke,
                            down: '#3936ed',
                            coml: '#60f260'
                        },
                        fill: {
                            ...prevState.colors.highlighter.fill,
                            down: '#0b08ce',
                            coml: '#1add1a'
                        }
                    }

                }
            }))
        }

        var sites = [];
        for (var [key] of data) {
            sites.push(key)
        }
        this.setState({ hightlighters: sites })
        console.log(this.state.hightlighters)
    }

    setSite = (site) => {
        console.log(site)
        DownStations['features'].reduce((prev, curr, index, arr) => {
            var corrd = { lat: curr['geometry']['coordinates'][1], lng: curr['geometry']['coordinates'][0] }
            var name = curr['properties']['STATION_ID']
            if (name === site) {
                this.setState(prevState => ({
                    target: {
                        ...prevState.target,
                        name: name,
                        coord: corrd
                    }
                }))
            }
        }, {})

        ComplianceStations['features'].reduce((prev, curr, index, arr) => {
            var corrd = { lat: curr['geometry']['coordinates'][1], lng: curr['geometry']['coordinates'][0] }
            var name = curr['properties']['STATION_ID']
            if (name === site) {
                this.setState(prevState => ({
                    target: {
                        ...prevState.target,
                        name: name,
                        coord: corrd
                    }
                }))
            }
        }, {})
    }

    makeOneMarker = (coord) => {
        console.log('making target marker')
        return (
            <Marker name='' position={coord} />
        )
    }

    onMapReady = () => {
        console.log('map ready')
    }

    onMapClicked = () => {
        console.log('map clicked');
    }

    makeBaseStation = (name, coord, textColor, strokeColor) => {
        const { google } = this.props
        return (
            <Marker position={coord}
                label={{
                    text: name,
                    color: textColor,
                    fontSize: '16px',
                    fontWeight: 'bold'
                }}
                icon={{
                    path: (google && google.maps.SymbolPath.CIRCLE),
                    strokeColor: strokeColor,
                    scale: 5,
                    labelOrigin: (google && new google.maps.Point(-10, -2))
                }}
            />
        )
    }

    makeSquareHighlighter = (name, coord, strokeColor, fillColor, fillOpacity) => {
        return (
            <Marker position={coord}
                icon={{
                    path: 'M -2,-2 2,-2 2,2 -2,2 z',
                    strokeColor: strokeColor,
                    fillColor: fillColor,
                    fillOpacity: fillOpacity,
                    scale: 5
                }}
            />
        )
    }

    makeZoneLable = (text, coord) => {
        return (
            <Marker
                position={coord}
                label={{
                    text: text,
                    color: '#FF0000',
                    fontSize: '16px',
                    fontWeight: 'bold'
                }}
                icon={{
                    path: 'M -2,-2 2,-2 2,2 -2,2 z',
                    fillOpacity: 0,
                    scale: 1
                }}
            />
        )
    }

    displayStations = (show) => {
        this.setState({ showStation: show })
    }

    displayBasemap = (show) => {
        console.log('show basemap:' + show)
        this.setState({ showBasemap: show })
    }

    render() {
        const { google } = this.props

        let baseStations = []

        let facilityFenceCoords = []
        Fence['features'][0]['geometry']['coordinates'].reduce((prev, curr, index, arr) => {
            let corrd = { lat: curr[1], lng: curr[0] }
            facilityFenceCoords.push(corrd)
        }, {})

        let highlighters = []
        let zoneLabels = []
        DownStations['features'].reduce((prev, curr, index, arr) => {
            var coord = { lat: curr['geometry']['coordinates'][1], lng: curr['geometry']['coordinates'][0] }
            var name = curr['properties']['STATION_ID']
            if( this.state.showStation)
                baseStations.push(this.makeBaseStation(name, coord, '#fff', '#75a1ea'))
            if (this.state.hightlighters.indexOf(name) >= 0) {
                highlighters.push(this.makeSquareHighlighter(name,
                    coord,
                    this.state.colors.highlighter.stroke.down,
                    this.state.colors.highlighter.fill.down, 0.7))
            }

            if (this.state.zoneLabels !== null && this.state.zoneLabels.has(name)) {
                let c = { lat: coord.lat - 0.0005, lng: coord.lng }
                zoneLabels.push(this.makeZoneLable(this.state.zoneLabels.get(name).toString(), c))
            }
        }, {})

        ComplianceStations['features'].reduce((prev, curr, index, arr) => {
            var coord = { lat: curr['geometry']['coordinates'][1], lng: curr['geometry']['coordinates'][0] }
            var name = curr['properties']['STATION_ID']
            if( this.state.showStation)
                baseStations.push(this.makeBaseStation(name, coord, '#fff', '#00e531'))
            if (this.state.hightlighters.indexOf(name) >= 0) {
                highlighters.push(this.makeSquareHighlighter(name,
                    coord,
                    this.state.colors.highlighter.stroke.coml,
                    this.state.colors.highlighter.fill.coml, 0.7))
            }
            if (this.state.zoneLabels !== null && this.state.zoneLabels.has(name)) {
                let c = { lat: coord.lat - 0.0005, lng: coord.lng }
                zoneLabels.push(this.makeZoneLable(this.state.zoneLabels.get(name).toString(), c))
            }
        }, {})


        let targetSite = this.makeOneMarker(this.state.target.coord)



        return (
            <Map
                google={google}
                mapType={'satellite'}
                zoom={12}
                onClick={this.onMapClicked}
                initialCenter={this.state.center}
                style={{ width: '100%', height: '100%', position: 'relative' }}
                className={'map'}
                onReady={this.onMapReady} >

                {baseStations}
                {highlighters}
                {targetSite}
                {zoneLabels}

                <Polygon
                    paths={facilityFenceCoords}
                    strokeColor="#fcbb64"
                    strokeOpacity={1.0}
                    strokeWeight={2}
                    fillColor="#f7f3e1"
                    fillOpacity={this.state.showBasemap === false ? 0 : 0.1}
                />
            </Map>
        )
    }

}

export default ComplexMap


