import React, { Component } from 'react';
import { Checkbox, Segment, Grid, Button, Menu, Image, Icon, Header, Accordion, Radio, Form, Divider, Dropdown } from 'semantic-ui-react'

class CtrlPanel extends Component {
    state = {
        activeIndex: 0,
        wells: 'All',
        detectionType: 'Detection',
        period: 'All',
        explorerParamGroups: 'All',
        explorerParam: 'All',
        explorerParamOptions: [],
        eventDates: [],
        eventDate: '',
        explorerParamGroupsZone: '',
        explorerParamOptionsZone: '',
        explorerParamZone: '',
        eventDateZone: '',
        zone: '',
        tobStations: true,
        tobBasemap: true
    }
    componentWillMount() {
        console.log('componentWillMount');
        fetch('http://54.213.115.18/ReachWcfRest/EAGLE_MPSW.svc/eventdates/', {
            method: 'GET'
        })
            .then((response) => response.json())
            .then((responseJson) => {
                let res = JSON.parse(responseJson['GetEventDatesResult'])
                var dates = []

                res.reduce((prev, curr, index, arr) => {
                    let obj = { text: curr['Edates'], value: curr['Edates'] }
                    dates.push(obj)
                }, {})
                this.setState({ eventDates: dates })
            })
    }

    handleClick = (e, titleProps) => {
        const { index } = titleProps
        const { activeIndex } = this.state
        const newIndex = activeIndex === index ? -1 : index

        this.setState({ activeIndex: newIndex })
    }

    handleWellsChange = (e, { value }) => this.setState({ wells: value })
    handelDetectionTypeChange = (e, { value }) => this.setState({ detectionType: value })
    handelPeriodChange = (e, { value }) => this.setState({ period: value })
    handleExplorerParamGroupsChange = (e, obj) => {
        console.log(obj)
        this.setState({ explorerParamGroups: obj['value'] });
        this.setState({ explorerParamOptions: this.explorerParamOptionsData[obj['value']] })
        console.log(this.state.explorerParamOptions)
    }

    handleExplorerParamChange = (e, { value }) => {
        this.setState({ explorerParam: value });
    }

    handleTimeQuerySubmit = () => {
        this.props.onTimeQuerySubmit(this.state)
    }

    handleZoneQuerySubmit = () => {
        this.props.onZoneQuerySubmit(this.state)
    }

    handleCustomPeriodChange = (e, { value }) => {
        this.setState({ eventDate: value });
    }

    handleExplorerParamGroupsZoneChange = (e, obj) => {
        this.setState({ explorerParamGroupsZone: obj['value'] });
        this.setState({ explorerParamOptionsZone: this.explorerParamOptionsZoneData[obj['value']] })
        console.log(this.state.explorerParamGroupsZone)
    }

    handelePeriodZoneChange = (e, { value }) => {
        this.setState({ eventDateZone: value });
    }

    zone = [
        { text: 'A', value: 'A' },
        { text: 'B', value: 'B' },
        { text: 'D', value: 'D' },
        { text: 'E', value: 'E' }
    ]

    handleExplorerParamZoneChange = (e, { value }) => {
        this.setState({ explorerParamZone: value });
    }

    handleZoneChange = (e, { value }) => {
        this.setState({ zone: value })
    }

    toggleShowStations = () => {
        let show = !this.state.tobStations;
        this.setState({ tobStations: show })
        this.props.onShowStations(show)
    }

    toggleShowBasemap = () => {
        let show = !this.state.tobBasemap;
        this.setState({ tobBasemap: show })
        this.props.onShowBaseMap(show);
    }

    explorerParamGroupOptionsZone = [
        { text: 'Field', value: 'Field' },
        { text: 'Metals', value: 'Metals' },
        { text: 'Major Anions', value: 'Major Anions' },
        { text: 'Major Cations', value: 'Major Cations' },
        { text: 'General', value: 'General' }
    ]

    explorerParamGroupOptions = [
        { text: 'All', value: 'All' },
        { text: 'Field', value: 'Field' },
        { text: 'Metals', value: 'Metals' },
        { text: 'Major Anions', value: 'Major Anions' },
        { text: 'Major Cations', value: 'Major Cations' },
        { text: 'General', value: 'General' }
    ]

    explorerParamOptionsData = {
        'All': [
            { text: 'All', value: 'All' }
        ],
        'Field': [
            { text: 'All', value: 'All' },
            { text: 'D.O.', value: 'D.O.' },
            { text: 'Flow', value: 'Flow' },
            { text: 'pH', value: 'pH' },
            { text: 'Specific Conductance', value: 'Specific Conductance' },
            { text: 'Temperature', value: 'Temperature' }
        ],

        'Metals': [
            { text: 'All', value: 'All' },
            { text: 'Aluminum', value: 'Aluminum' },
            { text: 'Antimony', value: 'Antimony' },
            { text: 'Arsenic', value: 'Arsenic' },
            { text: 'Barium', value: 'Barium' },
            { text: 'Beryllium', value: 'Beryllium' },
            { text: 'Boron', value: 'Boron' },
            { text: 'Cadmium', value: 'Cadmium' },
            { text: 'Chromium', value: 'Chromium' },
            { text: 'Cobalt', value: 'Cobalt' },
            { text: 'Copper', value: 'Copper' },
            { text: 'Iron', value: 'Iron' },
            { text: 'Lead', value: 'Lead' },
            { text: 'Lithium', value: 'Lithium' },
            { text: 'Manganese', value: 'Manganese' },
            { text: 'Mercury', value: 'Mercury' },
            { text: 'Molybdenum', value: 'Molybdenum' },
            { text: 'Nickel', value: 'Nickel' },
            { text: 'Selenium', value: 'Selenium' },
            { text: 'Silver', value: 'Silver' },
            { text: 'Strontium', value: 'Strontium' },
            { text: 'Thallium', value: 'Thallium' },
            { text: 'Uranium', value: 'Uranium' },
            { text: 'Vanadium', value: 'Vanadium' },
            { text: 'Zinc', value: 'Zinc' }
        ],
        'Major Anions': [
            { text: 'All', value: 'All' },
            { text: 'Alkalinity, Bicarbonate', value: 'Alkalinity, Bicarbonate' },
            { text: 'Chloride', value: 'Chloride' },
            { text: 'Nitrogen, Ammonia', value: 'Nitrogen, Ammonia' },
            { text: 'Nitrogen, Nitrate', value: 'Nitrogen, Nitrate' },
            { text: 'Nitrogen, Nitrite', value: 'Nitrogen, Nitrite' },
            { text: 'Phosphorus, Total', value: 'Phosphorus, Total' },
            { text: 'Sulfate', value: 'Sulfate' }
        ],
        'Major Cations': [
            { text: 'All', value: 'All' },
            { text: 'Calcium', value: 'Calcium' },
            { text: 'Magnesium', value: 'Magnesium' },
            { text: 'Potassium', value: 'Potassium' },
            { text: 'Sodium', value: 'Sodium' }
        ],
        'General': [
            { text: 'All', value: 'All' },
            { text: 'Hardness, (Calculated) as CaCO3', value: 'Hardness, (Calculated) as CaCO3' }
        ]
    }

    explorerParamOptionsZoneData = {
        'Field': [
            { text: 'D.O.', value: 'D.O.' },
            { text: 'ORP', value: 'ORP' },
            { text: 'pH', value: 'pH' },
            { text: 'Specific Conductance', value: 'Specific Conductance' },
            { text: 'Temperature', value: 'Temperature' }
        ],

        'Metals': [
            { text: 'Aluminum', value: 'Aluminum' },
            { text: 'Antimony', value: 'Antimony' },
            { text: 'Arsenic', value: 'Arsenic' },
            { text: 'Barium', value: 'Barium' },
            { text: 'Beryllium', value: 'Beryllium' },
            { text: 'Boron', value: 'Boron' },
            { text: 'Cadmium', value: 'Cadmium' },
            { text: 'Chromium', value: 'Chromium' },
            { text: 'Cobalt', value: 'Cobalt' },
            { text: 'Copper', value: 'Copper' },
            { text: 'Iron', value: 'Iron' },
            { text: 'Lead', value: 'Lead' },
            { text: 'Lithium', value: 'Lithium' },
            { text: 'Manganese', value: 'Manganese' },
            { text: 'Mercury', value: 'Mercury' },
            { text: 'Molybdenum', value: 'Molybdenum' },
            { text: 'Nickel', value: 'Nickel' },
            { text: 'Selenium', value: 'Selenium' },
            { text: 'Silver', value: 'Silver' },
            { text: 'Strontium', value: 'Strontium' },
            { text: 'Thallium', value: 'Thallium' },
            { text: 'Uranium', value: 'Uranium' },
            { text: 'Vanadium', value: 'Vanadium' },
            { text: 'Zinc', value: 'Zinc' }
        ],
        'Major Anions': [
            { text: 'Alkalinity, Bicarbonate', value: 'Alkalinity, Bicarbonate' },
            { text: 'Chloride', value: 'Chloride' },
            { text: 'Nitrogen, Ammonia', value: 'Nitrogen, Ammonia' },
            { text: 'Nitrogen, Nitrate', value: 'Nitrogen, Nitrate' },
            { text: 'Nitrogen, Nitrite', value: 'Nitrogen, Nitrite' },
            { text: 'Phosphorus, Total', value: 'Phosphorus, Total' },
            { text: 'Sulfate', value: 'Sulfate' }
        ],
        'Major Cations': [
            { text: 'Calcium', value: 'Calcium' },
            { text: 'Magnesium', value: 'Magnesium' },
            { text: 'Potassium', value: 'Potassium' },
            { text: 'Sodium', value: 'Sodium' }
        ],
        'General': [
            { text: 'Hardness, (Calculated) as CaCO3', value: 'Hardness, (Calculated) as CaCO3' }
        ]
    }

    render() {
        const { activeIndex } = this.state

        // const explorerParamOptions = [
        //     { text: 'All', value: 'All' },
        //     { text: 'Aluminum', value: 'Aluminum' },
        //     { text: 'Antimony', value: 'Antimony' },
        // ]


        return (
            <div style={{ textAlign: 'left' }}>
                <Accordion>
                    <Accordion.Title active={activeIndex === 0} index={0} onClick={this.handleClick}>
                        <Icon name='dropdown' />
                        Time period based query
                    </Accordion.Title>
                    <Accordion.Content active={activeIndex === 0}>
                        <Segment raised>
                            <Form>
                                <Form.Field>
                                    Choose Wells: <b>{this.state.wells}</b>
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='All Wells'
                                        name='radioGroup'
                                        value='All'
                                        checked={this.state.wells === 'All'}
                                        onChange={this.handleWellsChange}
                                    />
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Background Wells'
                                        name='radioGroup'
                                        value='Background'
                                        checked={this.state.wells === 'Background'}
                                        onChange={this.handleWellsChange}
                                    />
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Compliance Wells'
                                        name='radioGroup'
                                        value='Compliance'
                                        checked={this.state.wells === 'Compliance'}
                                        onChange={this.handleWellsChange}
                                    />
                                </Form.Field>                                
                            </Form>
                            <Divider />
                            <Form>
                                <Form.Field>
                                    Detection Type: <b>{this.state.detectionType}</b>
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Detections'
                                        name='radioGroup'
                                        value='Detection'
                                        checked={this.state.detectionType === 'Detection'}
                                        onChange={this.handelDetectionTypeChange}
                                    />
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Above Benchmark'
                                        name='radioGroup'
                                        value='AboveBM'
                                        checked={this.state.detectionType === 'AboveBM'}
                                        onChange={this.handelDetectionTypeChange}
                                    />
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Exceedance'
                                        name='radioGroup'
                                        value='Exceedance'
                                        checked={this.state.detectionType === 'Exceedance'}
                                        onChange={this.handelDetectionTypeChange}
                                    />
                                </Form.Field>
                            </Form>
                            <Divider />
                            <Form>
                                <Form.Field>
                                    For the period of: <b>{this.state.period}</b>
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Full Record'
                                        name='radioGroup'
                                        value='All'
                                        checked={this.state.period === 'All'}
                                        onChange={this.handelPeriodChange}
                                    />
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Q1'
                                        name='radioGroup'
                                        value='Q1'
                                        checked={this.state.period === 'Q1'}
                                        onChange={this.handelPeriodChange}
                                    />
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Q2'
                                        name='radioGroup'
                                        value='Q2'
                                        checked={this.state.period === 'Q2'}
                                        onChange={this.handelPeriodChange}
                                    />
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Q3'
                                        name='radioGroup'
                                        value='Q3'
                                        checked={this.state.period === 'Q3'}
                                        onChange={this.handelPeriodChange}
                                    />
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Q4'
                                        name='radioGroup'
                                        value='Q4'
                                        checked={this.state.period === 'Q4'}
                                        onChange={this.handelPeriodChange}
                                    />
                                </Form.Field>
                                <Dropdown text={this.state.eventDate}
                                    options={this.state.eventDates}
                                    onChange={this.handleCustomPeriodChange}
                                    fluid multiple selection
                                    style={{ visibility: (this.state.period === 'periodCustom') ? 'visible' : 'hidden' }}
                                />
                            </Form>
                            <Divider />
                            <Form>
                                <Form.Field>
                                    Explore parameters groups (optional): <b>{this.state.explorerParamGroups}</b>
                                </Form.Field>
                                <Dropdown text={this.state.explorerParamGroups} options={this.explorerParamGroupOptions} simple onChange={this.handleExplorerParamGroupsChange} />
                            </Form>
                            <Divider />
                            <Form>
                                <Form.Field>
                                    Explore parameters (optional): <b>{this.state.explorerParam}</b>
                                </Form.Field>
                                <Dropdown text={this.state.explorerParam} options={this.state.explorerParamOptions} simple onChange={this.handleExplorerParamChange} />
                            </Form>
                            <Divider />
                            <Button primary fluid onClick={this.handleTimeQuerySubmit}>Submit</Button>
                        </Segment>
                    </Accordion.Content>

                    <Accordion.Title active={activeIndex === 1} index={1} onClick={this.handleClick}>
                        <Icon name='dropdown' />
                        Zone based query
                    </Accordion.Title>
                    <Accordion.Content active={activeIndex === 1}>
                        <Segment raised>
                            <Form>
                                <Form.Field>
                                    Explore parameters groups (optional): <b>{this.state.explorerParamGroupsZone}</b>
                                </Form.Field>
                                <Dropdown text={this.state.explorerParamGroupsZone} options={this.explorerParamGroupOptionsZone} fluid selection onChange={this.handleExplorerParamGroupsZoneChange} />
                            </Form>
                            <Divider />
                            <Form>
                                <Form.Field>
                                    Explore parameters (optional): <b>{this.state.explorerParamZone}</b>
                                </Form.Field>
                                <Dropdown text={this.state.explorerParamZone} options={this.state.explorerParamOptionsZone} fluid selection onChange={this.handleExplorerParamZoneChange} />
                            </Form>
                            <Divider />
                            <Form>
                                <Form.Field>
                                    Choose event date: <b>{this.state.eventDateZone}</b>
                                </Form.Field>
                                <Dropdown text={this.state.eventDateZone}
                                    options={this.state.eventDates}
                                    onChange={this.handelePeriodZoneChange}
                                    fluid selection
                                />
                            </Form>
                            <Divider />
                            <Grid columns={3}>
                                <Grid.Column></Grid.Column>
                                <Grid.Column><Button primary onClick={this.handleZoneQuerySubmit}>Submit</Button></Grid.Column>
                                <Grid.Column><Button>Clear</Button></Grid.Column>
                            </Grid>
                        </Segment>
                    </Accordion.Content>

                    <Accordion.Title active={activeIndex === 2} index={2} onClick={this.handleClick}>
                        <Icon name='dropdown' />
                        Table of Contents
                    </Accordion.Title>
                    <Accordion.Content active={activeIndex === 2}>
                        <Segment raised>
                            <Checkbox label='GWDP Monitoring Stations' checked={this.state.tobStations} onClick={this.toggleShowStations} />
                            <Checkbox label='GWDP Basemap' checked={this.state.tobBasemap} onClick={this.toggleShowBasemap} />
                        </Segment>
                    </Accordion.Content>
                </Accordion>
            </div>
        )
    }
}

export default CtrlPanel;


